"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { useQuery } from "@tanstack/react-query";
import { UserCheck, UserPlus, Users } from "lucide-react";

import { Avatar, AvatarFallback, AvatarImage } from "@acme/ui/avatar";
import { Button } from "@acme/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@acme/ui/card";
import { Skeleton } from "@acme/ui/skeleton";

import { useTRPC } from "~/trpc/react";

// Define a type for the user
type UserData = {
  id?: string;
  name?: string | null;
  email?: string | null;
  image?: string | null;
};

export default function MembersPage() {
  const router = useRouter();
  const [user, setUser] = useState<UserData | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  // Use tRPC to get the session
  const trpc = useTRPC();
  const { data: sessionData, isLoading: isSessionLoading } = useQuery(
    trpc.auth.getSession.queryOptions(),
  );

  // Redirect to login if not authenticated
  useEffect(() => {
    if (!isSessionLoading && !sessionData?.user) {
      router.push("/api/auth/signin");
    } else if (sessionData?.user) {
      setUser({
        id: sessionData.user.id,
        name: sessionData.user.name,
        email: sessionData.user.email,
        image: sessionData.user.image,
      });
      setIsLoading(false);
    }
  }, [sessionData, isSessionLoading, router]);

  if (isLoading || isSessionLoading) {
    return <MembersPageSkeleton />;
  }

  return (
    <div className="container mx-auto py-10">
      <div className="mb-8 flex items-center justify-between">
        <div>
          <h1 className="text-4xl font-bold">Members Area</h1>
          <p className="text-lg text-muted-foreground">
            Welcome to the exclusive members area, {user?.name || "User"}!
          </p>
        </div>

        <div className="flex items-center gap-4">
          <Avatar className="h-12 w-12">
            <AvatarImage src={user?.image ?? ""} alt={user?.name ?? ""} />
            <AvatarFallback>
              {user?.name?.charAt(0).toUpperCase() ?? "U"}
            </AvatarFallback>
          </Avatar>
          <div>
            <p className="font-medium">{user?.name || "User"}</p>
            <p className="text-sm text-muted-foreground">{user?.email || ""}</p>
          </div>
        </div>
      </div>

      <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between">
            <div>
              <CardTitle>Member Benefits</CardTitle>
              <CardDescription>Exclusive perks for members</CardDescription>
            </div>
            <UserCheck className="h-8 w-8 text-primary" />
          </CardHeader>
          <CardContent>
            <ul className="list-inside list-disc space-y-2">
              <li>Unlimited video uploads</li>
              <li>Priority support</li>
              <li>Advanced analytics</li>
              <li>Customizable profiles</li>
              <li>Early access to new features</li>
            </ul>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between">
            <div>
              <CardTitle>Community</CardTitle>
              <CardDescription>Connect with other members</CardDescription>
            </div>
            <Users className="h-8 w-8 text-primary" />
          </CardHeader>
          <CardContent>
            <p className="mb-4">
              Join our vibrant community of creators and collaborate on
              projects, share insights, and grow together.
            </p>
            <Button className="w-full">Join Community Forum</Button>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between">
            <div>
              <CardTitle>Invite Others</CardTitle>
              <CardDescription>Grow our community</CardDescription>
            </div>
            <UserPlus className="h-8 w-8 text-primary" />
          </CardHeader>
          <CardContent>
            <p className="mb-4">
              Invite your colleagues and friends to join. For each referral,
              you'll receive additional storage and premium features.
            </p>
            <Button variant="outline" className="w-full">
              Generate Invite Link
            </Button>
          </CardContent>
        </Card>
      </div>

      <div className="mt-10">
        <h2 className="mb-4 text-2xl font-bold">Your Activity</h2>
        <div className="rounded-lg border p-6">
          <p className="text-muted-foreground">
            Your recent activity will be displayed here. Start creating content
            to see your activity.
          </p>
          <Button className="mt-4" asChild>
            <a href="/dashboard">Go to Dashboard</a>
          </Button>
        </div>
      </div>
    </div>
  );
}

function MembersPageSkeleton() {
  return (
    <div className="container mx-auto py-10">
      <div className="mb-8 flex items-center justify-between">
        <div>
          <Skeleton className="h-10 w-48" />
          <Skeleton className="mt-2 h-6 w-64" />
        </div>

        <div className="flex items-center gap-4">
          <Skeleton className="h-12 w-12 rounded-full" />
          <div>
            <Skeleton className="h-5 w-32" />
            <Skeleton className="mt-1 h-4 w-40" />
          </div>
        </div>
      </div>

      <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
        {[1, 2, 3].map((i) => (
          <Skeleton key={i} className="h-64 w-full" />
        ))}
      </div>

      <div className="mt-10">
        <Skeleton className="mb-4 h-8 w-40" />
        <Skeleton className="h-48 w-full" />
      </div>
    </div>
  );
}
